.offers{background-color:#33333a;display:flex;align-items:center;justify-content:center}.offers__text{font-size:18px;color:white;margin:20px}.offers__email{font-size:14px;text-transform:uppercase;padding:10px;width:360px;border:1px solid #666;margin:20px;background-color:#33333a;color:#fff}.offers__email::placeholder{color:white}.offers__btn{font-size:14px;padding:10px 35px;text-transform:uppercase;border:none;background-color:#fff;margin:20px;color:#3a3a3a}.menu{display:flex;align-items:center;justify-content:space-between;max-width:1040px;margin:auto;padding:28px 0}.menu__element{font-size:14px;text-transform:uppercase;color:#3a3a3a;border:none;margin:0 15px}.menu__text{font-size:14px;text-transform:uppercase;color:#3a3a3a;margin:0 15px}.menu__login{display:flex}.menu__icon{height:20px;margin:0 15px}.slider{position:relative;width:100%;background:url(./assets/images/for-slider.jpg) 0 30% no-repeat;background-size:cover;height:600px}.slider__content{width:550px;position:absolute;top:50%;left:50%;transform:translateY(-50%)}.slider__title{line-height:1.33;font-size:60px;color:#fff;margin:20px 0}.slider__link{color:#fff;padding:10px 0;transition:border 0.5s}.slider__link:hover{border-bottom:2px solid white}.slider__container{position:absolute;border-radius:50%;width:100px;height:100px;transition:background-color 0.5s}.slider__container:hover{background-color:rgba(128,128,128,0.8)}.slider__container_left{top:50%;transform:translate(-50%, -50%)}.slider__container_right{top:50%;right:0;transform:translate(50%, -50%)}.slider__btn{width:30px;height:30px;border-left:2px solid #fff;border-bottom:2px solid #fff;position:absolute}.slider__btn_left{top:50%;transform:translate(65px, -50%) rotate(45deg)}.slider__btn_right{top:50%;transform:translate(5px, -50%) rotate(225deg)}*{box-sizing:border-box}body{margin:0;padding:0;font-family:sans-serif}
